import React from 'react';
import { useCopyToClipboard } from 'usehooks-ts';

function SearchItem({item}){
  const litType = {'modernNovel': '', 'drama': ''};
  const type = item[9];

  const [copiedText, copy] = useCopyToClipboard();

  const handleClick = () => {
    let text = `${item[0]}, ${item[1]}\n${litType[type]}`;
    if(item[2] !== '') text += `\n: ${item[2]}`;
    if(item[3] !== '') text += `\n/: ${item[3]}`;
    if(item[6] !== '') text += `\n1: ${item[6]}`;
    if(item[5] !== '') text += `\n2: ${item[5]}`;
    if(item[4] !== '') text += `\n3: ${item[4]}`;
    if(item[7] !== '') text += `\nEBS: ${item[7]}`;
    if(item[8] !== '') text += `\n: ${item[8]}`;

    copy(text);
  };

  return(
    <div onClick={handleClick} className={`search-item search-item-${type}`}>
      <h3 style={{marginTop: 0, marginBottom: 0, fontSize: '1.3rem'}}>{item[0]}, {item[1]}</h3>
      <div>{litType[type]}</div>
      {item[2]===''?<></>:<div>
        <span className='search-detail-prefix'></span>
        <span>{item[2]}</span>
      </div>}
      {item[3]===''?<></>:<div>
        <span className='search-detail-prefix'>/</span>
        <span>{item[3]}</span>
      </div>}
      {item[6]===''?<></>:<div>
        <span className='search-detail-prefix'>1</span>
        <span>{item[6]}</span>
      </div>}
      {item[5]===''?<></>:<div>
        <span className='search-detail-prefix'>2</span>
        <span>{item[5]}</span>
      </div>}
      {item[4]===''?<></>:<div>
        <span className='search-detail-prefix'>3</span>
        <span>{item[4]}</span>
      </div>}
      {item[7]===''?<></>:<div>
        <span className='search-detail-prefix'>EBS</span>
        <span>{item[7]}</span>
      </div>}
      {item[8]===''?<></>:<div>
        <span className='search-detail-prefix'></span>
        <span>{item[8]}</span>
      </div>}
    </div>
  )
}

export default SearchItem;