// import logo from './logo.svg';
import './App.css';
import React, { useState } from 'react';
import SearchResult from './SearchResult';

function Title(){
  return(
    <div className='box'>
      <h1>   </h1>
      <div className='detail'>, , , EBS     .<span className='subdetail'>   ,  </span></div>
      <div className='detail'>      .</div>
    </div>
  )
}

function SearchForm({handleFormChange}){
  return(
    <div className='box'>
      <input type='text' onChange={handleFormChange} placeholder=' :   '></input>
    </div>
  )
}

function Credit(){
  return(
    <div className='row-container detail'>
      <div><a href='https://github.com/zanmulgyeol/litfind'>litfind: CSAT Literature Finder</a></div>
      <div className='detail'> 2025 <a href='https://github.com/zanmulgyeol'>zanmulgyeol</a></div>
    </div>
  )
}

function App(){
  const [keyword, setKeyword] = useState('');

  const handleFormChange = (e) => {
    setKeyword(e.target.value);
  }

  return(
    <div className='App'>
      <header>
        <Title></Title>
        <SearchForm handleFormChange={handleFormChange}></SearchForm>
      </header>
      <main className='box'>
        <SearchResult keyword={keyword}></SearchResult>
      </main>
      <footer>
        <Credit></Credit>
      </footer>
    </div>
  );
}

export default App;
